/*
 *  Rewards Details
 *    Scripts for switching tabs
 *
 *  Version:
 *    1.0.0 - 2021/12/10
 */

import { CLASS_INITIALISED, CLASS_VISIBLE, CLASS_ACTIVE } from "../../tools/constants/classes";


const SELECTOR_CONTENT = '[data-rewards-content]';
const SELECTOR_TAB = '[data-rewards-tab]';
const SELECTOR_CONTAINER = '[data-rewards-details]'

const ATTR_TAB_INDEX = 'data-tab-index';


document.querySelectorAll(SELECTOR_CONTAINER).forEach(container => {
  if (container.classList.contains(CLASS_INITIALISED)) return;
  container.classList.add(CLASS_INITIALISED);


  const tabs = container.querySelectorAll<HTMLElement>(SELECTOR_TAB);
  const contents = container.querySelectorAll<HTMLElement>(SELECTOR_CONTENT);

  // For every tab
  tabs.forEach(tab => {

    // On click
    tab.addEventListener('click', () => {

      // get the tab index
      let tabIndex = tab.getAttribute(ATTR_TAB_INDEX);

      // remove active class on all tabs
      tabs.forEach(t => {
        t.classList.remove(CLASS_ACTIVE);
      });

      // add active class to clicked tab
      tab.classList.add(CLASS_ACTIVE);

      // For each content element
      contents.forEach(content => {

        // get tab index
        let contentIndex = content.getAttribute(ATTR_TAB_INDEX);

        // remove visible class on content
        content.classList.remove(CLASS_VISIBLE);

        // If it has the same tab index as the tab that was clicked, add visible class
        if (tabIndex === contentIndex) content.classList.add(CLASS_VISIBLE);
      });
    });
  });
});